<template>
  <div class="body">
    <wv-header title="世界杯猜胜负">
	  <div class="btn-back" slot="right">
		<span class="iconfont icon-back" @click="loginout">退出</span>
	  </div>
	</wv-header>
	<p class="infoBox">{{info.name}}，您的金币：<span style="color:red" v-text="info.coins">500</span>。查看<router-link to="/history" style="color:#ffe70b;text-decoration: underline;">往期竞猜</router-link></p>
	<div class="matchBox">
	  <header>本期竞猜</header>
	  <div v-if="match_list.length === 0">没有比赛可以竞猜</div>
	  <div class="matchDiv" v-for="(m, i) in match_list">
	    <p>竞猜截止：{{m.guess_end_time}}</p>
		<wv-flex class="col-1">
		  <wv-flex-item flex="4">{{m.country_a_cn}}</wv-flex-item>
		  <wv-flex-item style="color:#fff;">VS</wv-flex-item>
		  <wv-flex-item flex="4">{{m.country_b_cn}}</wv-flex-item>
		</wv-flex>
		<wv-flex class="col-2">
		  <wv-flex-item>
		    <div>{{m.handicap_disc}}</div>
		  </wv-flex-item>
		  <wv-flex-item>
			<div v-if="m.flag === 'unjoined'">押 <wv-input type="number" class="moneyInput" v-model="m.pay_for"></wv-input> 金币</div>
			<div v-if="m.flag !== 'unjoined'">押 <span v-text="m.pay_for">0</span> 金币</div>
		  </wv-flex-item>
		</wv-flex>
		<wv-flex class="col-3" :gutter="5" v-if="m.flag === 'unjoined'">
		  <wv-flex-item>
			<button class="matchBtn" @click="setMatch(m, m.country_a)">支持{{m.country_a_cn}}<span style="font-size:10px;">{{'(' + m.odds_a + ')'}}</span></button>
		  </wv-flex-item>
		  <wv-flex-item>
			<button class="matchBtn" @click="setMatch(m, m.country_b)">支持{{m.country_b_cn}}<span style="font-size:10px;">{{'(' + m.odds_b + ')'}}</span></button>
		  </wv-flex-item>
		</wv-flex>
		<div class="col-3" v-if="m.flag === 'cannot_join'">您未参加此场比赛竞猜</div>
		<div class="col-3" v-if="m.flag === 'joined'">您支持{{m.support_country_cn}}胜，赔率为{{m.support_odds}}</div>
	  </div>
	</div>
	<div class="conBox">
	  <header class="head-title"> --活动规则--</header>
	  <ol>
	    <li>此活动参照的比分是90分钟内的比分，加时赛、点球的比分不计入。</li>
	    <li>初始金币为0，按照正负算值。</li>
	    <li>规则参照国际惯例（澳盘），每场比赛前1小时，关闭该场比赛的竞猜。</li>
	    <li>当天上午10点至次日上午10点为一个比赛日。上午10点之后公布昨日结算结果，上午10点当天比赛竞猜开始。</li>
	  </ol>
	</div>
	<div class="conBox">
	  <header class="head-title2">--财富排行榜--</header>
	  <ol class="">
	    <li v-for="m in ranking_list"><span style="float:right;" v-text="m.coins">10000</span><span v-text="m.user_name">张三</span></li>
	  </ol>
	</div>
  </div>
</template>
<script>
import services from '@/http/services'
import { Toast } from 'we-vue'
export default {
  name: 'index',
  data() {
    return {
      match_list: [], // 比賽信息
	  ranking_list: [], // 排行榜信息
	  info: {
	    name: '',
		coins: 0
	  } // 用戶信息
    }
  },
  methods:{
    setMatch: function (o, country) {
	  var self = this
	  let pay = o.pay_for
	  if (pay.toString().indexOf('.') > -1 || pay.toString().indexOf('-') > -1 || pay < 10 || pay > 500) {
	    Toast.fail({message:'请输入正整数的押金，且范围为10-500', duration: 2000})
		return
	  }
	  let postData = {
	    'schedule_id': o.schedule_id,
	    'country_name': country,
	    'condition_id': o.condition_id,
	    'pay_for': pay,
	  }
	  Toast.loading('laoding')
	  services.httpApi.set_cup(postData).then(function(resData){
        if (resData.code === 200) {
		  Toast.success(resData.msg)
		  self.getMatch()
		} else {
		  Toast.fail(resData.msg)
		}
      });
	},
	getInfo: function () {
	  var self = this
	  services.httpApi.userinfo().then(function(resData){
        if (resData.code === 200) {
		  self.info.name = resData.data.user_name
		  self.info.coins = resData.data.coins
		} else {
		  Toast.fail(resData.msg)
		}
      });
	},
    getMatch: function () {
	  var self = this
	  services.httpApi.get_info().then(function(resData){
        if (resData.code === 200) {
		  self.match_list = resData.data.schedules
		} else {
		  Toast.fail(resData.msg)
		}
      });
	},
	getRanking: function () {
	  var self = this
	  services.httpApi.get_ranking().then(function(resData){
        if (resData.code === 200) {
		  self.ranking_list = resData.data.ranking
		} else {
		  Toast.fail(resData.msg)
		}
      });
	},
	loginout: function () {
	  var self = this
	  services.httpApi.logout().then(function(resData){
        self.$router.push('/login');
      });
	}
  },
  created () {
    Toast.loading('laoding')
    this.getInfo()
	this.getMatch()
    this.getRanking()
  }
}
</script>

<style>

</style>



